
.sw-main{
  box-shadow: none;
  border: 1px solid darken($gray-300, 2%);
  
  .sw-toolbar{
      background: transparent;
      padding: 1.25rem;
  }
  .step-content{
      background-color: transparent !important;
  }
}

// sw-theme-default

.sw-theme-default{
  .step-content{
      background-color: transparent;
      padding: 1.25rem 1.25rem 0;
  }

  >ul.step-anchor{
      >li{
          >a{
              color: $gray-600;
              &:hover{
                  color: $gray-600; 
              }
              &:after{
                  background: $primary;
              }
          }
          &.active{
              >a{
                  color: $primary !important;
              }
          }
          &.done{
              >a{
                  color: $gray-600 !important;
                  &:after{
                      background: $success;
                  }
              }
          }
      }
  }
}
