.step-container {
  background: $white;
  padding: 30px;
  margin-bottom: 30px;
  display: flex;
  flex-wrap: wrap;
  border: none;
  border-radius: 0.5rem;
  li {
    flex: 1;
    background: $white;
    &:first-child {
      padding-left: 0;
    }
    .avatar {
      background: $gray-500;
    }
    .mdi-check {
      display: none;
    }
    &.done {
      opacity: 1;
      .mdi {
        display: none;
      }
      .mdi-check {
        display: block;
      }
      .avatar {
        background: $success;
      }
      h5 {
        color: $success;
      }
    }
    &.active {
      opacity: 1;
      .avatar {
        background: $primary;
      }
      h5 {
        color: $primary;
      }
    }
  }
  .step {
    display: flex;
    align-items: center;
    margin: 0;
    padding: 0;
    justify-content: center;
    h5 {
      color: $gray-500;
      // @include media-breakpoint-down(xs) {
      //   display: none;
      // }
    }
    &:after {
      display: none;
    }
  }
}


.form-wizard-header {
  margin-left: -1.25rem;
  margin-right: -1.25rem;
}