
.chatbox-wrapper {
  position: fixed;
  right: 24px;
  bottom: 10px;
  width: 300px;
  background-color: $card-bg;
  border-radius: $border-radius;
  box-shadow: $box-shadow;
  z-index: 99;

  .chat-input {
      .form-control {
          padding-right: 60px;
      }
  }

  .chat-link {
      position: absolute;
      right: 5px;
      top: 15px
  }
  
  &.minimize{
    @include transform(translateY(330px))
  }
}

/* Chat widget */
.chat-conversation-list {
  list-style: none;
  height: 250px;
  padding: 0 7px;

  li {
      margin-bottom: 24px;
  }

  .chat-avatar {
      float: left;
      text-align: center;
      width: 34px;

      img {
          border-radius: 100%;
          width: 100%;
      }

      i {
          font-size: 12px;
          font-style: normal;
      }
  }

  .ctext-wrap {
      background: lighten($primary, 42%);
      border-radius: $border-radius;
      display: inline-block;
      padding: 4px 12px;
      position: relative;
      min-width: 160px;

      i {
          display: block;
          font-size: 12px;
          font-style: normal;
          font-weight: $font-weight-bold;
          position: relative;
      }

      p {
          margin: 0;
          padding-top: 3px;
      }

      &:after {
          right: 98%;
          top: 0;
          border: solid transparent;
          content: " ";
          height: 0;
          width: 0;
          position: absolute;
          pointer-events: none;
          border-top-color: lighten($primary, 42%);
          border-width: 6px;
          margin-left: -1px;
          border-right-color: lighten($primary, 42%);
      }
  }

  .conversation-text {
      float: left;
      font-size: 12px;
      margin-left: 12px;
      width: 70%;
  }

  .odd {
      .chat-avatar {
          float: right !important;
      }

      .conversation-text {
          float: right !important;
          margin-right: 12px;
          text-align: right;
          width: 70% !important;
      }

      .ctext-wrap {
          background-color: $gray-300;
          color: $gray-700;
          &:after {
              border-color: transparent;
              border-left-color: $gray-300;
              border-top-color: $gray-300;
              left: 99% !important;
          }
      }
  }
}